@import "slick-carousel/slick/slick.css";
@import "slick-carousel/slick/slick-theme.css";
:root {
  --base-bg-color: #1d2029;
  --base-text-color: #ffffff;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Montserrat";
  padding-right: 0px !important;
  overflow-x: hidden !important;
}
html,
body {
  width: 100%;
  margin: 0;
  overflow-x: hidden; /* Prevent horizontal scrolling */
}
.slick-track {
  transition: transform 0.5s ease !important;
}

.slick-slide {
  transition: all 0.5s ease !important;
}
html {
  scroll-behavior: smooth;
}

.icon-image {
  width: 35px;
  height: 35px;
}
.typography-image .icon-image {
  width: 180px;
  height: 180px;
}
/* custon swiper style */
.swiper-button-prev,
.swiper-button-next {
  color: white;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}

.swiper-button-prev {
  left: 0;
}

.swiper-button-next {
  right: 0;
}

.swiper-pagination {
  bottom: 0px !important;
  text-align: center;
  position: absolute;
  width: 100%;
}

.noRightPadding {
  padding-right: 0px !important;
}

.swiper-pagination-bullet {
  width: 14px;
  height: 14px;
  background-color: #ccc !important;
  opacity: 1 !important;
  margin: 0 6px;
  transition: transform 0.3s ease;
}

.swiper-pagination-bullet-active {
  background-color: #e57c00 !important;
  transform: scale(1.8);
  margin-top: 10px;
}

.swiper .swiper-pagination {
  margin-top: 50px !important;
}
/*  .swiper-pagination {
  margin-top: 30px !important; /* adjust as needed * /}  */

.swiper-pagination {
  bottom: -10px !important; /* or any value you need */
  position: relative; /* or override absolute if needed */
}
/* 
That light blue background you see on autocomplete suggestions 
(especially on Chrome) is the browserâ€™s default styling for autofill 
or autocomplete.

To prevent or style it differently,
you can override the browser's autofill styles using CSS.  */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 1000px transparent inset !important; /* Change 'white' to your input background */
  -webkit-text-fill-color: #000 !important; /* text color */
  transition: background-color 5000s ease-in-out 0s;
}
.fw-500 {
  font-weight: 500 !important;
}
.fw-600 {
  font-weight: 600 !important;
}
.fw-700 {
  font-weight: 700 !important;
}
.fw-800 {
  font-weight: 800 !important;
}
.fw-900 {
  font-weight: 900 !important;
}
.my-base-button-style {
  background-color: var(--base-text-color) !important;
  color: var(--base-bg-color) !important;
}

.my-base-bg-color-Class {
  background-color: var(--base-bg-color) !important;
  color: var(--base-text-color) !important;
}

.my-margin-center-x {
  margin-left: auto;
  margin-right: auto;
}

